@import "~scss/variables";

.sw-cms-el-config-zen-features {
    .has-border-top {
        padding-top: 20px;
        border-top: 1px solid $color-gray-300;
    }

    &__content-option {
        display: grid;
        grid-template-columns: 8fr 1fr 8fr;
        gap: 0 16px;

        .sw-cms-el-config-zen-features {
            &__icon {
                padding: 12px 16px;
                margin: auto;

                svg {
                    width: 25px;
                    height: 25px;
                }
            }
        }
    }

    &__setting-option {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(185px, 1fr));
        gap: 0 32px;
    }
}