@import "~scss/variables";

.sw-cms-el-preview-zen-code {
    width: 100%;

    &-headline {
        margin-bottom: 10px;
        border-bottom: 1px solid #d1d9e0;
        padding-bottom: 5px;
        font-size: 14px;
    }

    &__wrapper {
        display: grid;
        grid-template-columns: auto 1fr;
        align-items: stretch;
        grid-gap: 8px;
        color: $color-gray-500;
        background: $color-gray-50;
        border-radius: 8px;
        padding: 8px;

        .sw-cms-el-preview-zen-code__icon_col {
            display: flex;
            align-items: center;
        }

        .sw-cms-el-preview-zen-code__icon {
            display: flex;
            align-items: center;
            justify-content: center;
            color: $color-gray-500;
            width: 32px;
            height: 32px;
        }

        .sw-cms-el-preview-zen-code__text_col {
            border-left: 2px solid $color-gray-200;
            padding: 0 8px;
        }

    }

    &__placeholder {
        > div {
            margin: 8px 0;
            height: 0.6em;
            border-radius: 4px;
            background-color: $color-gray-200;
            width: 80%;
        }

        > div ~ div {
            width: 90%;
        }

        > div ~ div ~ div {
            width: 60%;
        }
    }
}
