@import "~scss/variables";

.sw-cms-el-preview-zen-text-banner {
    height: 100%;
    border-radius: 3px;
    overflow: hidden;

    &__row {
        height: 100%;
    }

    &__col {
        height: 100%;
    }

    &__text_banner {
        position: relative;
        height: 100%;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__content-container {
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 0;
        transform: translate(-50%, -50%);
        width: 100%;
        max-width: 120px;
        text-align: center;
    }

    &__text {
        display: inline-block;
        height: 8px;
        width: 25px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 50px;
        margin-left: 5px;
    }
}