.sw-cms-block-zen-grid-3-3-3-3-el-2 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        align-content: stretch;
        align-items: start;
        grid-gap: 40px;

        .is--tablet-landscape & {
            grid-template-columns: repeat(2, 1fr);
        }

        .is--desktop & {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    .sw-cms-slot {
        + & {
            margin-top: 2px;
        }
    }

    &__side-elements {
        height: 100%;
    }

    &__top {
        margin-bottom: 40px;
    }
}
