@import "~scss/variables";

.sw-cms-preview-zen-layout-advanced-grid-one-reversed {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    padding: 15px;

    .left-top {
        grid-column: 1/3;
        grid-row: 1;
    }

    .left-bottom-left {
        grid-column: 1;
        grid-row: 2;
    }

    .left-bottom-right {
        grid-column: 2;
        grid-row: 2;
    }

    .right {
        grid-column: 3;
        grid-row: 1/3;
    }
}