@import "~scss/variables";

.sw-cms-preview-zen-code {
    width: 100%;
    padding: 20px;

    &__wrapper {
        display: grid;
        grid-template-columns: auto 1fr;
        align-items: stretch;
        color: $color-gray-700;
        background: $color-gray-100;
        border-radius: 8px;
        padding: 16px;

        .sw-cms-preview-zen-code__icon_col {
            display: flex;
            align-items: center;
        }

        .sw-cms-preview-zen-code__icon {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 16px;
            color: #b3bfcc;
            width: 48px;
            height: 48px;
        }

        .sw-cms-preview-zen-code__text_col {
            border-left: 2px solid $color-gray-200;
            padding: 0 8px;
        }
    }

    &__placeholder {
        > div {
            margin: 8px 0;
            height: 0.6em;
            border-radius: 4px;
            background-color: $color-gray-200;
            width: 50%;
        }

        > div ~ div {
            width: 60%;
        }

        > div ~ div ~ div {
            width: 30%;
        }
    }
}
