@import "~scss/variables";

.sw-cms-block-zen-grid-7-5 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;

        .is--tablet-landscape & {
            grid-template-columns: 7fr 5fr;
        }

        .is--desktop & {
            grid-template-columns: 7fr 5fr;
        }
    }
}