@import "~scss/variables";

.sw-cms-preview-zen-teaser-grid-4-4-4 {
    &__row {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 10px;
        padding: 15px;
    }

    &__teaser {
        position: relative;
        height: 160px;
        border-radius: 4px;
        overflow: hidden;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__text-container {
        position: absolute;
        bottom: 8px;
        left: 8px;
        border-radius: 3px;
        padding: 4px 8px;
        background: #fff;
        font-size: 0;
    }

    &__text {
        display: inline-block;
        height: 6px;
        width: 17px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 32px;
        margin-left: 5px;
    }
}