.sw-cms-block-zen-layout-simple-8-4 {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 40px;

    .is--tablet-landscape & {
        grid-template-columns: 8fr 4fr;
    }

    .is--desktop & {
        grid-template-columns: 8fr 4fr;
    }

    .sw-cms-section__sidebar & {
        grid-template-columns: 1fr;
    }

    &__side-elements {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;
        height: 100%;
    }
}
