@import "~scss/variables";

.sw-cms-el-preview-zen-features {
    width: 100%;

    .sw-cms-el-preview-zen-features__headline {
        margin-bottom: 10px;
        border-bottom: 1px solid #d1d9e0;
        padding-bottom: 5px;
    }

    .sw-cms-el-preview-zen-features__wrapper {
        display: grid;
        grid-template-columns: 1fr;
        color: #b3bfcc;

        .sw-cms-el-preview-zen-features__element {
            display: grid;
            grid-template-columns: auto 1fr;
            align-items: center;
            margin: 4px 10px;
        }

        .sw-cms-el-preview-zen-features__icon {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 10px;
            color: #b3bfcc;

            &.sw-icon--small {
                width: 12px;
                height: 12px;
            }
        }

        .sw-cms-el-preview-zen-features__text {
            height: 0.6em;
            border-radius: 4px;
            background-color: $color-gray-100;
        }
    }
}
