@import "~scss/variables";

.sw-cms-el-preview-zen-grid-placeholder {
    display: flex;
    width: 100%;
    height: 100%;

    &__inner {
        display: flex;
        width: 100%;
        color: $color-gray-700;
        background: $color-gray-100;
        border-radius: 8px;
        border: 2px dashed $color-gray-600;

        [class*='sw-cms-block-']:hover & {
            color: $color-shopware-brand-500;
        }
    }

    &__empty {
        text-align: center;
        cursor: pointer;
        display: flex;
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 20px 20px;
        z-index: 300;
        pointer-events: none;
    }

    &__icon {
        margin-bottom: 6px;
    }

    // overlay styles
    ~ .sw-cms-slot__preview-overlay {
        border: none;
    }

    ~ .sw-cms-slot__overlay {
        border: 2px solid $color-shopware-brand-500;
        border-radius: 8px;

        &:hover {
            color: $color-shopware-brand-500;
            background: $color-module-blue-50;
        }

        .sw-cms-slot__actions {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            top: 0;
            right: 0;
            border: none;
            background: none;
        }

        .sw-cms-slot__element-action {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            opacity: 0;
        }

        .sw-cms-slot__settings-action {
            display: none;
        }
    }
}