@import "~scss/variables";

.sw-cms-el-zen-footer-navigation {
  width: 100%;
  border: 1px solid #dadee5;
  border-radius: 4px;
  background: $color-white;
  padding: 10px;

  .sw-cms-el-zen-footer-placeholder-listing div {
    margin: 15px 30px;
    height: 1.5em;
    border-radius: 4px;
    background-color: $color-gray-100;

    &.sw-cms-el-zen-footer-navigation__placeholder--subcategory {
      margin: 15px 30px 15px 70px;
    }
  }

  hr {
    &.sw-cms-el-zen-footer-navigation__divider {
      max-width: 90%;
      margin: 10px auto;
      border: 1px solid $color-gray-300;
    }
  }

  h2 {
    margin: 10px;
  }
}