.sw-cms-block-zen-gallery-description-heading-buybox {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 15px;

    &.is--mobile {
        grid-template-columns: repeat(auto-fit, minmax(195px, 1fr));
    }

    .sw-cms-block-zen-gallery-description-heading-buybox__left {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 15px;
    }

    .sw-cms-block-zen-gallery-description-heading-buybox__left-bottom {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 15px;
    }

    .sw-cms-block-zen-gallery-description-heading-buybox__right {
        display: flex;
        flex-direction: column;
        grid-gap: 15px;
    }

    .sw-cms-block-zen-gallery-description-heading-buybox__right-top {
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-gap: 15px;
    }

    .sw-cms-block-zen-gallery-description-heading-buybox__right-center {
        display: flex;
        flex: 1;
    }

    &:not(.is--mobile) {
        .sw-cms-block-zen-gallery-description-heading-buybox__left {
            grid-area: 1 / 1 / 2 / 2;
        }

        .sw-cms-block-zen-gallery-description-heading-buybox__left-bottom {
            grid-area: 2 / 1 / 3 / 2;
        }

        .sw-cms-block-zen-gallery-description-heading-buybox__right {
            grid-area: 1 / 2 / 3 / 3;
        }
    }
}
