@import "~scss/variables";

.sw-cms-block-zen-grid-6-3-3 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;

        .is--tablet-landscape & {
            grid-template-columns: 6fr 3fr 3fr;
        }

        .is--desktop & {
            grid-template-columns: 6fr 3fr 3fr;
        }
    }
}