@import "~scss/variables";

.sw-cms-block-zen-grid-8-4 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;

        .is--tablet-landscape & {
            grid-template-columns: 8fr 4fr;
        }

        .is--desktop & {
            grid-template-columns: 8fr 4fr;
        }
    }
}