@import "~scss/variables";

.sw-cms-el-zen-teaser {
    display: flex;
    height: 100%;

    &__container {
        display: flex;
        position: relative;
        overflow: hidden; // ... because of border-radius

        &.is--cover {
            height: 100%;
            width: 100%; //new
        }
    }

    &__image-container {
        display: flex;

        img {
            display: block;
            max-width: 100%;
        }

        &.is--cover {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;

            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }

        &.is--stretch {
            img {
                width: 100%;
            }
        }
    }

    &__overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    &__content-container {
        display: flex;
        position: relative;
        width: 100%;

        &:not(.is--cover) {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }

    &__text {
        font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), var(--zen-font-size-max))"};
        padding: 8px 16px;
        margin: 20px;
        border-radius: 2px;
    }
}
