@import "~scss/variables";

.sw-cms-el-zen-image-slider {
    position: relative;
    display: flex;
    height: 100%;

    &.has--outside-arrows {
        width: calc(100% - 48px);
        margin: 0 auto;
    }

    &.has--outside-dots {
        padding-bottom: 30px;
    }

    &__container {
        position: relative;
        display: flex;
        width: 100%;
        overflow: hidden; // ... because of border-radius
    }

    &__image-container {
        display: flex;

        &.is--cover {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;

            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }

        &.is--stretch {
            img {
                width: 100%;
            }
        }
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    &__content-container {
        display: flex;
        width: 100%;

        &:not(.is--cover) {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }

    &__text {
        position: relative;
        padding: 8px 16px;
        margin: 20px 10%;
        border-radius: 2px;

        &.is-placeholder {
            width: 100%;
            max-width: 600px;

            .is--mobile & {
                width: 60%;
            }
        }
    }

    .sw-text-editor {
        &.is--heading-lg {
            --zen-font-headline-scale: 2.5;

            h1, .h1 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(36px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h2, .h2 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(28px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h3, .h3 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(24px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h4, .h4 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(20px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h5, .h5 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(16px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h6, .h6 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(14px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }
        }

        &.is--text-lg {
            --zen-font-text-scale: 1.5;

            p, div, a {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(16px * var(--zen-font-text-scale)))"};
            }
        }

        .sw-text-editor__content-editor {
            h1,
            h2,
            h3,
            h4,
            h5,
            h6,
            p,
            div,
            ul li,
            ol li,
            blockquote,
            blockquote::before {
                color: inherit;
            }
        }
    }
}

.sw-cms-el-zen-image-slider__placeholder {
    > div {
        margin: 10px 0;
        height: 16px;
        border-radius: 4px;
        background-color: $color-gray-100;
        width: 70%;

        .is--mobile & {
            margin: 5px 0;
            height: 8px;
        }
    }

    > div ~ div {
        width: 100%;
    }

    > div ~ div ~ div {
        width: 50%;
    }
}

.sw-cms-el-zen-image-slider__navigation {
    .is--nav-outside {
        //justify-content: center;
        //background: #fff;
        //border-radius: 5px;
        //padding: 15px;

        .sw-cms-el-zen-image-slider__navigation-button {
            color: $color-gray-400;
        }
    }

    .sw-cms-el-zen-image-slider__navigation-button {
        cursor: pointer;
        color: $color-gray-100;

        &:hover {
            color: $color-gray-900;
        }
    }
}

.sw-cms-el-zen-image-slider__navigation-arrows {
    .sw-cms-el-zen-image-slider__navigation-button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }

    &.is--dot-outside {
        .sw-cms-el-zen-image-slider__navigation-button {
            top: calc(50% - 15px);
        }
    }

    &.is--nav-inside {
        .sw-cms-el-zen-image-slider__navigation-button {
            &:first-child {
                left: 24px;
            }

            &:last-child {
                right: 24px;
            }
        }
    }

    &.is--nav-outside {
        .sw-cms-el-zen-image-slider__navigation-button {
            &:first-child {
                left: -25px;
            }

            &:last-child {
                right: -25px;
            }
        }
    }
}

.sw-cms-el-zen-image-slider__navigation-dots {
    .sw-cms-el-zen-image-slider__navigation-buttons {
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translate(-50%, -50%);
        display: grid;
        grid-auto-flow: column dense;
        grid-auto-columns: min-content;

        .sw-cms-el-zen-image-slider__navigation-button {
            width: 15px;
            height: 15px;
            background: $color-gray-100;
            border-radius: 100%;
            margin: 0 3px;

            &.is--active {
                background: $color-gray-900;
            }

            &:hover {
                background: $color-gray-900;
            }
        }
    }

    &.is--dot-outside {
        .sw-cms-el-zen-image-slider__navigation-buttons {
            bottom: -6px;

            .sw-cms-el-zen-image-slider__navigation-button {
                background: $color-gray-400;

                &.is--active {
                    background: $color-gray-900;
                }

                &:hover {
                    background: $color-gray-900;
                }
            }
        }
    }
}
