@import "~scss/variables";

.sw-cms-preview-zen-grid-3-3-3-3 {
    &__row {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 10px;
        padding: 15px;
    }

    .sw-cms-preview-zen-placeholder {
        .sw-icon {
            ~ span {
                font-size: 10px;
            }
        }
    }
}