.sw-cms-block-zen-layout-simple-teaser {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(195px, 1fr));
    grid-gap: 40px;

    .sw-cms-el-image.is--cover {
        min-height: 300px;
    }

    .sw-cms-block-zen-layout-simple-teaser__side-images {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;
        height: 100%;
    }
}
