.sw-cms-block-zen-layout-simple-4-8-reversed {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 40px;

    .is--tablet-landscape & {
        grid-template-columns: 4fr 8fr;
    }

    .is--desktop & {
        grid-template-columns: 4fr 8fr;
    }

    .sw-cms-section__sidebar & {
        grid-template-columns: 1fr;
    }

    &__side-elements {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;
        height: 100%;
    }
}
