@import "~scss/variables";

.sw-cms-el-preview-zen-breadcrumb {
    width: 100%;

    .sw-cms-el-preview-zen-breadcrumb__headline {
        margin-bottom: 10px;
        border-bottom: 1px solid #d1d9e0;
        padding-bottom: 5px;
    }

    .sw-cms-el-preview-zen-breadcrumb__wrapper {
        display: grid;
        grid-template-columns: 1fr 10px 1fr 10px 1fr 10px;
        grid-row-gap: 6px;
        color: #b3bfcc;
        margin: 16px 0 0 0;

        .sw-cms-el-preview-zen-breadcrumb__element {
            height: 0.6em;
            border-radius: 4px;
            background-color: $color-gray-100
        }

        .sw-cms-el-preview-zen-breadcrumb__divider {
            display: flex;
            align-items: center;
            justify-content: center
        }

        .sw-cms-el-preview-zen-breadcrumb__divider .sw-icon--small {
            width: 8px;
            height: 8px;
            color: #b3bfcc
        }
    }
}
