@import "~scss/variables";

.sw-cms-preview-zen-breadcrumb {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: stretch;
    padding: 20px;

    .sw-cms-preview-zen-breadcrumb__wrapper {
        display: flex;
        color: #b3bfcc;
        margin: 16px 0 0 0;

        .sw-cms-preview-zen-breadcrumb__element {
            height: 0.6em;
            border-radius: 4px;
            background-color: $color-gray-100;
            width: 75px;
            margin: 0 5px
        }

        .sw-cms-preview-zen-breadcrumb__divider {
            display: flex;
            align-items: center;
            justify-content: center
        }

        .sw-cms-preview-zen-breadcrumb__divider .sw-icon--small {
            width: 8px;
            height: 8px;
            color: #b3bfcc
        }
    }
}
