@import "~scss/variables";

.sw-cms-preview-zen-image-slider {
    align-items: center;
    display: grid;
    grid-template-columns: 1fr;
    height: 100%;

    padding: 15px;

    &__inner {
        position: relative;
        height: 100%;
        border-radius: 4px;
        overflow: hidden;
    }

    .sw-icon {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
    }

    .icon--regular-chevron-left-s {
        left: 3px;
    }

    .icon--regular-chevron-right-s {
        right: 3px;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__placeholder {
        position: absolute;
        top: 50%;
        left: 36px;
        right: 36px;
        transform: translateY(-50%);

        > div {
            margin: 8px 0;
            height: 0.6em;
            border-radius: 4px;
            background-color: $color-gray-100;
            width: 50%;
        }

        > div ~ div {
            width: 60%;
        }

        > div ~ div ~ div {
            width: 30%;
        }
    }
}
