@import "~scss/variables";

.sw-cms-el-config-zen-image-slider {
    .sw-tabs-item {
        display: flex;
        align-items: center;
    }

    .has-border-top {
        padding-top: 20px;
        border-top: 1px solid $color-gray-300;
    }

    .sw-media-list-selection-v2 {
        margin-bottom: 20px;
    }

    &__preview {
        height: 36px;
        width: 44px;
        position: relative;

        img.sw-media-preview-v2__item {
            border-radius: 4px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            max-height: 100%;
            max-width: 100%;
        }
    }

    &__setting-option {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(185px, 1fr));
        gap: 0 32px;
    }

    &__setting-option--small {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
        gap: 0 32px;
    }

    .sw-text-editor + &__editor-scale {
        position: relative;
        margin-top: -50px;
        z-index: 1;
        padding: 0 10px;
        display: flex;
    }

    &__editor-scale {
        .sw-field--switch .sw-field__label label {
            padding-top: 5px;
            padding-bottom: 5px;
        }
    }

    &__setting-display-mode {
        grid-column: 1 / -1;
    }

    &__link-prefix {
        height: 36px;
        width: 44px;
        border-radius: 2px;
        object-fit: cover;
    }

    &__link-input {
        margin-bottom: 8px;

        .sw-field__addition {
            &.is--prefix {
                padding: 5px 0 5px 5px;
                background: initial;
                border-right: none;
            }
        }
    }

    &__element-border-radius-inner,
    &__text-border-radius-inner,
    &__image-border-radius-inner,
    &__overlay-inner {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 32px;
    }

    &__text-paddings-inner,
    &__text-margins-inner {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 16px;
    }

    .sw-field--switch:not(.label-height) {
        margin-top: 0;
    }
}
