@import "~scss/variables";

.sw-cms-el-zen-divider {
    display: flex;
    width: 100%;
    padding: 8px 0;

    &__wrapper {
        display: flex;
        width: 100%;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    &__divider {
        flex: 1;
    }

    &__text {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 0 16px;
    }
}
