.sw-cms-block-zen-layout-advanced-grid-one {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 40px;

    .is--tablet-landscape &,
    .is--desktop & {
        grid-template-columns: repeat(3, 1fr);

        .sw-cms-slot-left {
            grid-column: 1;
            grid-row: 1/3;
        }

        .sw-cms-slot-right-top {
            grid-column: 2/4;
            grid-row: 1;
        }

        .sw-cms-slot-right-bottom-left {
            grid-column: 2;
            grid-row: 2;
        }

        .sw-cms-slot-right-bottom-right {
            grid-column: 3;
            grid-row: 2;
        }
    }

    .sw-cms-section__sidebar & {
        grid-template-columns: 1fr;
    }
}
