@mixin addUnitBadge($unit) {
    position: relative;

    &::after {
        content: $unit;
        position: absolute;
        top: 50%;
        right: 16px;
        transform: translateY(-50%);
        pointer-events: none;
        opacity: 0.5;
        font-size: 14px;
        font-weight: 500;
        color: #52667a;
    }
}

@mixin addIconBadge($iconName, $icon) {
    position: relative;

    &::before {
        content: url($icon);
        position: absolute;
        top: 50%;
        left: -5px;
        pointer-events: none;
        font-size: 12px;
        font-weight: 500;
        color: inherit;
        background: #ffffff;
        border-radius: 100%;
        border: 6px solid #fff;
        @if $iconName == "exclamation" {
            transform: translateY(-60%);
            width: 11px;
            height: 11px;
        } @else {
            transform: translateY(-50%);
            width: 13px;
            height: 13px;
        }
    }
}

@mixin addBadge($content) {
    position: relative;

    &::after {
        content: $content;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        pointer-events: none;
        font-size: 12px;
        font-weight: 500;
        color: inherit;
        background: #f0f2f5;
        @if $content == "offcanvas" {
            padding: 3px 9px;
        } @else {
            padding: 3px 8px;
        }
        border-radius: 4px;
    }
}

@mixin addStatusBadge($content) {
    position: relative;

    &::after {
        content: $content;
        position: absolute;
        top: 50%;
        right: -7px;
        transform: translateY(-50%);
        pointer-events: none;
        font-size: 12px;
        font-weight: 500;
        color: inherit;
        background: #f0f2f5;
        padding: 2px 3px;
        border-radius: 7px;
        border: 3px solid #ffffff;
    }
}

