            .sw-field-id-zen-pagination-active-color,
            .sw-field-id-zen-pagination-active-border-color
            {
                .sw-inherit-wrapper__inheritance-label {
                    @include addStatusBadge(':active');
                }
            }

            // add ':disabled' badge into input field
            .sw-field-id-zen-pagination-disabled-bg,
            .sw-field-id-zen-pagination-disabled-color,
            .sw-field-id-zen-pagination-disabled-border-color
            {
                .sw-inherit-wrapper__inheritance-label {
                    @include addStatusBadge(':disabled');
                }
            }

            // add 'sticky' badge into input field
            .sw-field-id-zen-header-sticky-shadow,
            .sw-field-id-zen-sticky-header-size,
            .sw-field-id-zen-sticky-header-height
            {
                .sw-inherit-wrapper__inheritance-label {
                    @include addStatusBadge(':sticky');
                }
            }
        }
    }

}

.sw-popover__wrapper {
    z-index: 999;
}