@import "~scss/variables";

.sw-cms-block-zen-grid-4-8 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;

        .is--tablet-landscape & {
            grid-template-columns: 4fr 8fr;
        }

        .is--desktop & {
            grid-template-columns: 4fr 8fr;
        }
    }
}