@import "~scss/variables";

.sw-cms-el-zen-search-banner {
    display: flex;
    height: 100%;

    &__container {
        display: flex;
        position: relative;
        overflow: hidden; // ... because of border-radius

        &.is--cover {
            height: 100%;
            width: 100%;
        }
    }

    &__image-container {
        display: flex;

        img {
            display: block;
            max-width: 100%;
        }

        &.is--cover {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;

            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }

        &.is--stretch {
            img {
                width: 100%;
            }
        }
    }

    &__overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    &__content-container {
        display: flex;
        position: relative;
        width: 100%;

        &:not(.is--cover) {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        column-gap: 1.5rem;
        row-gap: 1.5rem;
        padding: 8px 16px;
        margin: 20px;
        border-radius: 2px;
    }

    .sw-text-editor {
        &.is--heading-lg {
            --zen-font-headline-scale: 2.5;

            h1, .h1 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(36px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h2, .h2 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(28px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h3, .h3 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(24px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h4, .h4 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(20px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h5, .h5 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(16px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h6, .h6 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(14px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }
        }

        &.is--text-lg {
            --zen-font-text-scale: 1.5;

            p, div, a {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(16px * var(--zen-font-text-scale)))"};
            }
        }

        &.is--empty {
            display: none;
        }

        .sw-text-editor__content-editor {
            h1,
            h2,
            h3,
            h4,
            h5,
            h6,
            p,
            div,
            ul li,
            ol li,
            blockquote,
            blockquote::before {
                color: inherit;
            }
        }
    }

    .search-input-group {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        width: 100%;

        .form-control {
            display: block;
            width: 100%;
            padding: 0.5625rem 1rem;
            font-size: 0.875rem;
            font-weight: 400;
            line-height: 1.5;
            color: #010a10;
            background-color: #F0F1F2;
            background-clip: padding-box;
            border: 1px solid #F0F1F2;
            appearance: none;
            border-radius: 100px;
            outline-offset: -2px;
            -webkit-appearance: textfield;

            &:focus {
                outline: 0;
            }
        }

        > .form-control {
            position: relative;
            flex: 1 1 auto;
            width: 1%;
            min-width: 0;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }

        .form-control-lg {
            min-height: calc(1.5em + (1.25rem + 2px));
            padding: 0.5rem 2rem;
            font-size: 1.25rem;
        }

        > .btn {
            margin-left: -1px;
            border-radius: 100px;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            background: #F0F1F2;
            border: 1px solid #F0F1F2;
            border-left: 0;
            font-weight: 600;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            display: inline-block;
            padding: 2px 0.75rem;
            line-height: 34px;
            color: #333333;
            text-align: center;
            vertical-align: middle;
            cursor: pointer;
            user-select: none;
            transition: all 0.3s ease;
        }

        .icon {
            width: 22px;
            height: 22px;
            display: inline-flex;
            align-self: center;
            font-size: inherit;
            overflow: visible;
            color: #4a545b;

            > svg {
                top: 6px;
                width: 100%;
                height: 100%;
                position: relative;
                fill: currentColor;
                vertical-align: middle;
            }
        }
    }
}
