@import "~scss/variables";

.sw-cms-el-zen-breadcrumb {
    width: 100%;
    background: #fff;
    padding: 20px 20px;

    .sw-cms-el-zen-breadcrumb__wrapper {
        display: flex;
        color: #b3bfcc;

            .sw-cms-el-zen-breadcrumb__element {
                height: 25px;
                border-radius: 4px;
                background-color: $color-gray-100;
                width: 100px;
                margin: 0 5px
            }

            .sw-cms-el-zen-breadcrumb__divider {
                display: flex;
                align-items: center;
                justify-content: center
            }

            .sw-cms-el-zen-breadcrumb__divider .sw-icon--small {
                width: 12px;
                height: 12px;
                color: #b3bfcc
            }
        }
}
