@import "~scss/variables";
$zen-prefix: zen-;

:root {
    --#{$zen-prefix}font-size-scale: 8vw;
    --#{$zen-prefix}font-size-min: .5rem;
    --#{$zen-prefix}font-size-max: 16px;
}

.sw-cms-sidebar__block-preview {
    > [class^="sw-cms-block-preview-zen"],
    > [class^="sw-cms-el-preview-zen"] {
        overflow: hidden;
        position: relative;
    }
}

.sw-cms-preview-zen-badge {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 32px;
    height: 32px;
    z-index: 1;
    padding: 14px 14px 4px 4px;

    &::before {
        content: '';
        position: absolute;
        bottom: -5px;
        left: -20px;
        transform: rotate(45deg);
        border-right: solid 30px transparent;
        border-left: solid 30px transparent;
        border-top: solid 30px $color-gray-300;
        z-index: -1;
        border-radius: 100%;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.sw-cms-preview-zen-placeholder {
    display: flex;
    width: 100%;
    height: 100%;
    color: $color-gray-700;
    background: $color-gray-100;
    border: 2px dashed $color-gray-600;
    font-size: $font-size-xxs;
    text-align: center;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 8px;
    padding: 5px;
    min-height: 75px;

    &__icon {
        margin: 4px 0;
    }
}
