@import "~scss/variables";

.sw-cms-preview-zen-grid-24-24-24-24-24 {
    &__row {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap: 10px;
        padding: 15px;
    }

    .sw-cms-preview-zen-placeholder {
        .sw-icon {
            ~ span {
                display: none;
            }
        }
    }
}