@import "~scss/variables";

.sw-cms-preview-zen-layout-simple-8-4-reversed {
    display: grid;
    grid-template-columns: 8fr 4fr;
    grid-gap: 10px;
    padding: 15px;

    &__side-elements {
        height: 100%;
        display: grid;
        grid-template-rows: 96px 96px;
        grid-gap: 10px;
    }
}
