@import "~scss/variables";

.sw-cms-preview-zen-teaser-grid-3-3-3-3 {
    &__row {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 10px;
        padding: 15px;
    }

    &__teaser {
        position: relative;
        height: 140px;
        border-radius: 4px;
        overflow: hidden;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__text-container {
        position: absolute;
        bottom: 6px;
        left: 6px;
        border-radius: 3px;
        padding: 3px 6px;
        background: #fff;
        font-size: 0;
    }

    &__text {
        display: inline-block;
        height: 5px;
        width: 12px;
        border-radius: 1px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 20px;
        margin-left: 3px;
    }
}