@import "~scss/variables";

.sw-cms-preview-zen-text-banner {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
        padding: 15px;
    }

    &__text-banner {
        position: relative;
        height: 200px;
        border-radius: 4px;
        overflow: hidden;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__content-container {
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 0;
        transform: translate(-50%, -50%);
        width: 100%;
        max-width: 180px;
       text-align: center;
    }

    &__text {
        display: inline-block;
        height: 12px;
        width: 35px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 65px;
        margin-left: 5px;
    }
}