@import "~scss/variables";

.sw-cms-el-config-zen-divider {
    .has-border-top {
        padding-top: 20px;
        border-top: 1px solid $color-gray-300;
    }

    &__setting-option {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(185px, 1fr));
        gap: 0 32px;
    }

    .sw-field--switch {
        margin-top: 0;
    }
}