@import "~scss/variables";

.sw-cms-el-preview-zen-search-banner {
    height: 100%;
    border-radius: 3px;
    overflow: hidden;

    &__row {
        height: 100%;
    }

    &__col {
        height: 100%;
    }

    &__search {
        position: relative;
        height: 100%;
        border-radius: 4px;
        overflow: hidden;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__content-container {
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 0;
        transform: translate(-50%, -50%);
        width: 100%;
        max-width: 120px;
        text-align: center;
    }

    &__text {
        display: inline-block;
        height: 8px;
        width: 25px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 50px;
        margin-left: 5px;
    }

    &__search-container {
        display: flex;
        margin: 6px 0;
    }

    &__search-input {
        height: 18px;
        width: calc(100% - 18px);
        border-top-left-radius: 100px;
        border-bottom-left-radius: 100px;
        background-color: $color-gray-100;
    }

    &__search-input-btn {
        position: relative;
        height: 18px;
        width: 18px;
        border-top-right-radius: 100px;
        border-bottom-right-radius: 100px;
        background-color: $color-gray-100;

        .icon {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 12px;
            height: 12px;
            display: inline-flex;
            align-self: center;
            color: $color-gray-900;

            > svg {
                width: 100%;
                height: 100%;
                top: 0.25em;
                position: relative;
                fill: currentColor;
            }
        }
    }
}