@import "~scss/variables";

.sw-cms-preview-zen-teaser-grid-12 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
        padding: 15px;
    }

    &__teaser {
        position: relative;
        height: 200px;
        border-radius: 4px;
        overflow: hidden;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__text-container {
        position: absolute;
        bottom: 16px;
        left: 16px;
        border-radius: 3px;
        padding: 6px 12px;
        background: #fff;
        font-size: 0;
    }

    &__text {
        display: inline-block;
        height: 12px;
        width: 35px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 65px;
        margin-left: 5px;
    }
}