@import "~scss/variables";

.sw-cms-block-zen-grid-2-8-2 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;

        .is--tablet-landscape & {
            grid-template-columns: 2fr 8fr 2fr;
        }

        .is--desktop & {
            grid-template-columns: 2fr 8fr 2fr;
        }
    }
}