@import "~scss/variables";

.sw-cms-preview-zen-divider {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: stretch;
    padding: 20px;

    &__wrapper {
        height: 100%;
        display: grid;
        grid-template-columns: 1fr;
        color: #b3bfcc;
    }

    &__placeholder {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50px;
        border-radius: 6px;
        background-color: $color-gray-50;
        color: $color-gray-600;
        font-size: $font-size-xxs;
        text-transform: uppercase;
        width: 100%;
    }

    &__divider {
        width: 100%;
        height: 2px;
        border-radius: 2px;
        background-color: $color-gray-600;
        margin: 6px 0;
    }
}
