@import "~scss/variables";

.sw-cms-preview-zen-features {
    width: 100%;
    padding: 20px;

    .sw-cms-preview-zen-features__wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        color: #b3bfcc;
        margin: 16px 0 0 0;

        .sw-cms-preview-zen-features__element {
            display: grid;
            grid-template-columns: auto 1fr;
            align-items: center;
            margin: 4px;
        }

        .sw-cms-preview-zen-features__icon {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 4px;
            color: #b3bfcc;

            &.sw-icon--small {
                width: 12px;
                height: 12px;
            }
        }

        .sw-cms-preview-zen-features__text {
            height: 0.6em;
            border-radius: 4px;
            background-color: $color-gray-100;
        }
    }
}

