@import "~scss/variables";

.sw-cms-el-zen-text-banner {
    display: flex;
    height: 100%;

    &__container {
        display: flex;
        position: relative;
        overflow: hidden; // ... because of border-radius

        &.is--cover {
            height: 100%;
            width: 100%; //new
        }
    }

    &__image-container {
        display: flex;

        img {
            display: block;
            max-width: 100%;
        }

        &.is--cover {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;

            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }

        &.is--stretch {
            img {
                width: 100%;
            }
        }
    }

    &__overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    &__content-container {
        display: flex;
        position: relative;
        width: 100%;

        &:not(.is--cover) {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }

    &__content {
        padding: 8px 16px;
        margin: 20px;
        border-radius: 2px;
    }

    .sw-text-editor {
        &.is--heading-lg {
            --zen-font-headline-scale: 2.5;

            h1, .h1 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(36px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h2, .h2 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(28px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h3, .h3 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(24px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h4, .h4 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(20px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h5, .h5 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(16px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }

            h6, .h6 {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(14px * var(--zen-font-headline-scale)))"};
                line-height: 1.2;
            }
        }

        &.is--text-lg {
            --zen-font-text-scale: 1.5;

            p, div, a {
                font-size: #{"clamp( var(--zen-font-size-min), var(--zen-font-size-scale), calc(16px * var(--zen-font-text-scale)))"};
            }
        }

        &.is--empty {
            display: none;
        }

        .sw-text-editor__content-editor {
            h1,
            h2,
            h3,
            h4,
            h5,
            h6,
            p,
            div,
            ul li,
            ol li,
            blockquote,
            blockquote::before {
                color: inherit;
            }
        }
    }
}
