@import '~scss/variables';
@import "~scss/mixins";

.sw-cms-preview-zen-gallery-heading-buybox {
    display: grid;
    grid-template-columns: 7fr 5fr;
    padding: 15px 5px 15px 15px;
    height: 175px;

    &__right-top {
        height: 20%;
    }

    &__right-bottom {
        height: 80%;
    }

    &__gallery {
        display: grid;
        grid-template-columns: 22px 1fr;
        grid-gap: 4px;
    }

    &__images {
        display: grid;
        grid-auto-rows: 22px;
        grid-gap: 4px;

        img {
            border: 1px solid $color-gray-300;
            border-radius: 2px;
            display: block;
            object-fit: cover;
            width: 100%;
            height: 100%;
            padding: 4px;
        }
    }

    &__slider {
        position: relative;

        img {
            margin: 0;
            display: block;
            object-fit: cover;
            width: 100%;
            overflow: hidden;
            height: 120px;
        }

        span {
            position: absolute;
            z-index: 1;
            top: calc(50% - 8px);

            &:first-child {
                left: 2px;
            }

            &:last-child {
                right: 2px;
            }
        }
    }

    &__buybox {
        height: 100%;
    }

    .sw-cms-el-preview-buy-box__price {
        font-size: $font-size-m;
    }
}

.sw-cms-preview-zen-gallery-heading-buybox-heading {
    display: grid;
    grid-template-columns: 5fr 1fr;
    grid-column-gap: 5px;
    padding: 0 10px;

    &__name {
        @include flex-centering-vertical;

        color: $color-darkgray-200;
        font-size: 10px;
        line-height: 1;
        font-weight: $font-weight-semi-bold;
    }

    &__logo {
        @include flex-centering-vertical;

        &::before {
            @include circle(10px);

            content: '';
            margin-right: 2px;
            background: $color-gray-100;
        }
    }

    &__placeholder-container {
        display: flex;
        flex-direction: column;
    }

    &__placeholder {
        height: 3px;
        background: $color-gray-100;
        border-radius: $border-radius-pill;

        &:first-child {
            width: 15px;
        }

        &:last-child {
            width: 23px;
        }

        &:not(:first-child) {
            margin: 2px 0 0;
        }
    }
}