@import "~scss/variables";

.sw-cms-preview-zen-search-banner {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
        padding: 15px;
    }

    &__search-banner {
        position: relative;
        height: 200px;
        border-radius: 4px;
        overflow: hidden;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__content-container {
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 0;
        transform: translate(-50%, -50%);
        width: 100%;
        max-width: 180px;
       text-align: center;
    }

    &__text {
        display: inline-block;
        height: 12px;
        width: 35px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 65px;
        margin-left: 5px;
    }

    &__search-container {
        display: flex;
        margin: 8px 0;
    }

    &__search-input {
        height: 24px;
        width: calc(100% - 24px);
        border-top-left-radius: 100px;
        border-bottom-left-radius: 100px;
        background-color: $color-gray-100;
    }

    &__search-input-btn {
        position: relative;
        height: 24px;
        width: 24px;
        border-top-right-radius: 100px;
        border-bottom-right-radius: 100px;
        background-color: $color-gray-100;

        .icon {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 12px;
            height: 12px;
            display: inline-flex;
            align-self: center;
            color: $color-gray-900;

            > svg {
                width: 100%;
                height: 100%;
                top: 0.25em;
                position: relative;
                fill: currentColor;
            }
        }
    }
}