@import "~scss/variables";

.sw-cms-el-zen-code {
    width: 100%;

    &__wrapper {
        display: grid;
        grid-template-columns: 100%;
        align-items: stretch;
        grid-gap: 32px;
        color: $color-gray-500;
        background: $color-gray-50;
        border-radius: 8px;
        padding: 32px;

        .sw-cms-el-zen-code__icon_col {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .sw-cms-el-zen-code__icon {
            display: flex;
            align-items: center;
            justify-content: center;
            color: $color-gray-500;
            width: 80px;
            height: 80px;
            margin-bottom: 32px;
        }

        .sw-cms-el-zen-code__text_col {
            display: flex;
            align-items: center;
            flex-direction: column;
        }

        .sw-cms-el-zen-code__text {
            width: 100%;

            ~ .sw-cms-el-zen-code__text {
                margin-top: 16px;
            }
        }

        .sw-cms-el-zen-code__text_headline {
            margin-bottom: 0;
        }

        .sw-cms-el-zen-code__text_inner {
            border-left: 2px solid $color-gray-200;
            padding: 0 16px;
            margin: 16px 0 16px 0;

            pre {
                font-size: 14px;
                text-wrap: wrap;
                word-break: break-all;
                white-space: pre-wrap;
            }
        }

        .sw-cms-el-zen-code__placeholder {
            width: 100%;

            .sw-cms-el-zen-code__text_inner {

                > div {
                    margin: 8px 0;
                    height: 0.6em;
                    border-radius: 4px;
                    background-color: $color-gray-200;
                    width: 50%;
                }

                > div ~ div {
                    width: 60%;
                }

                > div ~ div ~ div {
                    width: 30%;
                }
            }
        }
    }
}

.sw-cms-block-zen-code {
    .sw-cms-el-zen-code {
        &__wrapper {
            grid-template-columns: auto 1fr;

            .sw-cms-el-zen-code__icon {
                margin-bottom: 0;
            }
        }
    }
}

.sw-cms-section__sidebar,
.is--mobile {
    .sw-cms-block-zen-code {
        .sw-cms-el-zen-code {
            &__wrapper {
                grid-template-columns: 100%;
            }
        }
    }
}
