@import "~scss/variables";

.sw-cms-el-config-zen-teaser {
    .has-border-top {
        padding-top: 20px;
        border-top: 1px solid $color-gray-300;
    }

    &__setting-option {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(185px, 1fr));
        gap: 0 32px;
    }

    .sw-media-upload-v2 {
        margin-bottom: 22px;

        .sw-media-upload-v2__header {
            display: none;
        }
    }

    &__mapping-preview {
        display: grid;
        width: 100%;
        height: 200px;
        justify-items: center;
        align-items: start;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            overflow: hidden;
        }

        &__preview-info {
            width: 100%;
        }
    }

    &__text-border-radius-inner,
    &__image-border-radius-inner {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 32px;
    }

    &__text-paddings-inner,
    &__text-margins-inner {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 16px;
    }

    .sw-field--switch {
        margin-top: 0;
    }
}