.sw-cms-block-zen-gallery-heading-buybox {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 15px;

    &.is--mobile {
        grid-template-columns: repeat(auto-fit, minmax(195px, 1fr));
    }

    .sw-cms-block-zen-gallery-heading-buybox__right {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 15px;
    }

    .sw-cms-block-zen-gallery-heading-buybox__right-top {
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-gap: 15px;
    }
}
