@import "~scss/variables";

.sw-cms-preview-zen-teaser-grid-6-6 {
    &__row {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 10px;
        padding: 15px;
    }

    &__teaser {
        position: relative;
        height: 180px;
        border-radius: 4px;
        overflow: hidden;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__text-container {
        position: absolute;
        bottom: 12px;
        left: 12px;
        border-radius: 3px;
        padding: 5px 10px;
        background: #fff;
        font-size: 0;
    }

    &__text {
        display: inline-block;
        height: 8px;
        width: 15px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 28px;
        margin-left: 5px;
    }
}