@import "~scss/variables";

.sw-cms-preview-zen-grid-3-3-3-3-el-2 {
    &__row {
        display: grid;
        grid-template-columns: 4fr 4fr 4fr 4fr;
        grid-gap: 10px;
        padding: 15px;
    }

    &__col {
        display: grid;
        grid-gap: 2px;
    }

    .sw-cms-preview-zen-placeholder {
        .sw-icon {
            ~ span {
                font-size: 10px;
            }
        }
    }
}
