@import "~scss/variables";

.sw-cms-block-zen-grid-4-4-4 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;

        .is--tablet-landscape & {
            grid-template-columns: repeat(3, 1fr);
        }

        .is--desktop & {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}