@import "~scss/variables";

.sw-cms-block-zen-teaser-grid-3-3-3-3 {
    &__row {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 40px;
        padding: 15px;

        .is--tablet & {
            grid-template-columns: repeat(2, 1fr);
        }

        .is--desktop & {
            grid-template-columns: repeat(4, 1fr);
        }
    }
}