@import "~scss/variables";

.sw-cms-el-config-zen-text-banner {
    .has-border-top {
        padding-top: 20px;
        border-top: 1px solid $color-gray-300;
    }

    &__setting-option {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(185px, 1fr));
        gap: 0 32px;
    }

    .sw-text-editor + &__editor-scale {
        position: relative;
        margin-top: -50px;
        z-index: 1;
        padding: 0 10px;
        display: flex;
    }

    &__editor-scale {
        .sw-field--switch .sw-field__label label {
            padding-top: 5px;
            padding-bottom: 5px;
        }
    }

    .sw-media-upload-v2 {
        margin-bottom: 22px;

        .sw-media-upload-v2__header {
            display: none;
        }
    }

    &__mapping-preview {
        display: grid;
        width: 100%;
        height: 200px;
        justify-items: center;
        align-items: start;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            overflow: hidden;
        }

        &__preview-info {
            width: 100%;
        }
    }

    &__content-border-radius-inner,
    &__image-border-radius-inner {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 32px;
    }

    &__content-paddings-inner,
    &__content-margins-inner {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 16px;
    }

    .sw-field--switch {
        margin-top: 0;
    }
}