@import "~scss/variables";

.sw-cms-el-preview-zen-teaser {
    height: 100%;
    border-radius: 3px;
    overflow: hidden;

    &__row {
        height: 100%;
    }

    &__col {
        height: 100%;
    }

    &__teaser {
        position: relative;
        height: 100%;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__text-container {
        position: absolute;
        bottom: 10px;
        left: 10px;
        border-radius: 3px;
        padding: 4px 10px;
        background: #fff;
        font-size: 0;
    }

    &__text {
        display: inline-block;
        height: 8px;
        width: 25px;
        border-radius: 2px;
        background-color: $color-gray-100;
    }

    &__text ~ &__text {
        width: 50px;
        margin-left: 5px;
    }
}