// page
.zen-theme-gravity-index {
    p {
        margin-bottom: 16px;
    }

    .smart-bar__header {
        img {
            width: 24px;
            height: auto;
            vertical-align: middle;
        }

        .sw-label {
            vertical-align: middle;
            min-width: 0;
            margin-bottom: 0;
        }
    }

    .zen-theme-gravity-index__description {
        margin: 100px 0 40px 0;
        color: #a3b3c2;
        text-align: center;
    }

    .zen-theme-gravity-index__tabs {
        display: grid;
        grid-template-columns: minmax(126px, 1fr) minmax(0, 800px) 1fr;
        justify-items: stretch;
    }

    .zen-theme-gravity-index__styling-grid {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 30px;
        align-content: start;
    }

    .zen-theme-gravity-index__saleschannel-link {
        max-width: 140px;
    }

    .zen-theme-gravity-index__grid-item-labels {
        margin: 0 auto 10px auto;
        text-align: center;

        .sw-label {
            margin: 0;
        }

        .sw-label__caption {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            display: inline-block;
            width: 100%;
        }
    }

    .zen-theme-gravity-index__saleschannel-link {
        .sw-label {
            cursor: pointer;
        }
    }

    .zen-theme-gravity-index__settings-grid,
    .zen-theme-gravity-index__support-grid,
    .zen-theme-gravity-index__documentation-grid {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
        grid-gap: 30px 10px;
    }

    .zen-theme-gravity-index__changelog-grid {
        .sw-description-list {
            dt, dd {
                padding: 4px 8px;
            }
        }
    }

    .zen-theme-gravity-index__footer {
        text-align: center;
        margin: 40px 0;

        .zen-theme-gravity-index__footer-text {
            color: #a3b3c2;
            font-size: 14px;
            margin-bottom: 10px;
            -webkit-animation: focus-in-expand 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) 2s both;
            animation: focus-in-expand 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) 2s both;
        }

        .zen-theme-gravity-index__footer-logo {
            &::after {
                display: none;
            }
        }
    }

    a[target="_blank"] {
        &.zen-theme-gravity-settings-item {
            color: #52667a;
            text-decoration: none;
            display: flex;
            align-items: center;

            &::after {
                background-color: #52667a;
            }
        }
    }
}

@-webkit-keyframes focus-in-expand {
    0% {
        letter-spacing: -0.5em;
        -webkit-filter: blur(12px);
        filter: blur(12px);
        opacity: 0;
    }
    100% {
        -webkit-filter: blur(0px);
        filter: blur(0px);
        opacity: 1;
    }
}
@keyframes focus-in-expand {
    0% {
        letter-spacing: -0.5em;
        -webkit-filter: blur(12px);
        filter: blur(12px);
        opacity: 0;
    }
    100% {
        -webkit-filter: blur(0px);
        filter: blur(0px);
        opacity: 1;
    }
}


