@import "~scss/variables";

.sw-cms-el-zen-features {
    width: 100%;

    .sw-cms-el-zen-features__wrapper {
        display: grid;
        color: #b3bfcc;

        .is--mobile & {
            grid-auto-flow: row;
        }

        .is--tablet-landscape & {
            grid-template-columns: 1fr 1fr;
        }

        .is--desktop & {
            grid-auto-flow: column;
        }

        .sw-cms-section__sidebar & {
            grid-auto-flow: row;
        }

        .sw-cms-el-zen-features__element {
            display: grid;
            grid-template-columns: auto 1fr;
            align-items: center;
            margin: 10px;

            svg {
                width: inherit;
                height: inherit;
            }
        }

        .sw-cms-el-zen-features__icon {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 10px;
            color: #b3bfcc;

            &.sw-icon--small {
                width: 20px;
                height: 20px;
            }
        }

        .sw-cms-el-zen-features__text {
            &:empty {
                height: 25px;
                border-radius: 4px;
                background-color: $color-gray-100;
            }
        }
    }
}
